  APP1: "Having trained in psychodynamic and person-centred approaches to counselling, I aim to create a confidential, empathic, and containing space between us, to explore what is happening in your life. I would describe therapeutic work as relational because, from my experience, I have found it fundamentally an authentic soul-to-soul encounter.",
